import React, { useState, useContext } from "react";
import {getStatus, updateStatus, getName, getsurName} from  '../../../../../store/profile-reduser'
import Profile_about_top from "./Profile_about_top";


import { connect } from "react-redux";
import { AuthContext } from "../../../../../context/Auth-contex";
import { useEffect } from "react";
import { withRouter } from "react-router-dom";




const  Profile_about_top_cont = (props) => {
  let auth = useContext(AuthContext)
  useEffect(() =>{props.getStatus(props.match.params.userId); props.getName(props.match.params.userId); props.getsurName(props.match.params.userId)}, [props.userId])
  
   
  return <Profile_about_top id = {props.userId} myUserId = {auth.userId} surName ={props.surName} name = {props.name} stat = {props.status} updateStatus ={props.updateStatus}/>;

}
let mapStateToProps = (state) =>{
 return{
    status: state.Profile.status,
    name: state.Profile.name,
    surName: state.Profile.surName
}
}
let mapDispatchToProps = {
  getStatus,
  updateStatus,
  getName,
  getsurName
}
let withRouterProfile = withRouter(Profile_about_top_cont)

export default connect(mapStateToProps, mapDispatchToProps)(withRouterProfile);
