import React, { useState, useContext } from "react";
import * as axios from "axios";
import {
  getName,
  getsurName,
  getGender,
  getDateBirth,
  getHomeTown,
  getLanguages,
  getRelationship,
  getWith,
  updateProfile,
  updateProfile2,
  updateProfile3,
  updateProfile4,
  updateProfile5,
  updateProfile6,
  updateProfile7
} from "../../store/profile-reduser";

import { connect } from "react-redux";
import { AuthContext } from "../../context/Auth-contex";
import { useEffect } from "react";
import Edit_page from "./Edit_page";
import Axios from "axios";

const Edit_page_const = (props) => {
  
  let auth = useContext(AuthContext);

  return (
    <Edit_page
      gender={props.gender}
      relationship={props.relationship}
      With={props.With}
      dateBirth={props.dateBirth}
      homeTown={props.homeTown}
      languages={props.languages}
      id={auth.userId}
      updateProfile={props.updateProfile}
      updateProfile2={props.updateProfile2}
      updateProfile3={props.updateProfile3}
      updateProfile4={props.updateProfile4}
      updateProfile5={props.updateProfile5}
      updateProfile6={props.updateProfile6}
      updateProfile7={props.updateProfile7}
      surName={props.surName}
      name={props.name}


    />
  );
};
let mapStateToProps = (state) => {
  return {
    name: state.Profile.name,
    surName: state.Profile.surName,
    gender: state.Profile.gender,
    relationship: state.Profile.relationship,
    With: state.Profile.With,
    dateBirth: state.Profile.dateBirth,
    homeTown: state.Profile.homeTown,
    languages: state.Profile.languages,
  };
};
let mapDispatchToProps = {
  getName,
  getsurName,
  updateProfile,
  updateProfile2,
  updateProfile3,
  updateProfile4,
  updateProfile5,
  updateProfile6,
  updateProfile7,
  getGender,
  getDateBirth,
  getHomeTown,
  getLanguages,
  getRelationship,
  getWith,
};

export default connect(mapStateToProps, mapDispatchToProps)(Edit_page_const);
