import React from "react";
import "../Edit_page.css";
import Header_edit_page_contacts from "../header_edit_page/Header_edit_page_contacts";
import { useState } from "react";
import * as axios from "axios";
import { useEffect } from "react";

const Contact_page = (props) => {
  let [view, setView] = useState(false);
  function changeView() {
    if (view == false) {
      setView(true);
    }
  }

  let [country, setCountry] = useState('');

  let [city, setCity] = useState('');
  let [phone, setPhone] = useState('');
  let [dopPhone, setDopPhone] = useState('');
  let [dis, setDis] = useState('');
  let [webSite, setWebSite] = useState('');
  function getProfileForEditt() {
    axios
      .get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.id}`)
      .then((response) => {
        setCountry(response.data.country);
        setCity(response.data.city)
        setPhone(response.data.phone)
        setDopPhone(response.data.dopPhone)
        setDis(response.data.dis)
        setWebSite(response.data.webSite)
      });
     
  }
  
 

 useEffect(() =>{ getProfileForEditt()}, [props.id])

  let refCountry = React.createRef();
  let refCity = React.createRef();
  let refPhone = React.createRef();
  let refDopPhone = React.createRef();
  let refDis = React.createRef();
  let refWebSite = React.createRef();

  function changeHandlerCountry(e) {
    setCountry(e.currentTarget.value);
  }
  function changeHandlerCity(e) {
    setCity(e.currentTarget.value);
  }
  function changeHandlerPhone(e) {
    setPhone(e.currentTarget.value);
  }
  function changeHandlerDopPhone(e) {
    setDopPhone(e.currentTarget.value);
  }
  function changeHandlerDis(e) {
    setDis(e.currentTarget.value);
  }
  function changeHandlerWebSite(e) {
    setWebSite(e.currentTarget.value);
  }


  function updProfile2() {
    props.updateProfile2(
      props.id,
      refCountry.current.value,
      refCity.current.value,
      refPhone.current.value,
      refDopPhone.current.value,
      refDis.current.value,
      refWebSite.current.value,
    );
  }

  return (
    <div className="wrapper_edit_page" id ="wrapper_edit_page_contacts">
      <Header_edit_page_contacts />
      {view && (
        <div className="wrapper_saved_edit" >
          <img src="https://vk.com/images/icons/msg_ok.png?1" alt="" />
          <div className="text_saved_edit">
            <span className="sp_changes_saved">Изменения сохранены.</span>
            <span className="sp_changes_will_see">
              Новые данные будут отражены на Вашей странице.
            </span>
          </div>
        </div>
      )}
      <div className="flex_input_edit">
        <label for="input">Страна:</label>
        <input type="text" value ={country} onChange ={changeHandlerCountry} ref = {refCountry}/>
      </div>
      <div className="flex_input_edit">
        <label>Город:</label>
        <input type="text" value ={city} ref = {refCity} onChange ={changeHandlerCity}/>
      </div>
      <div id="polosa_edit" class="polosa_bottom_about_me"></div>
      <div className="flex_input_edit">
        <label>Мобильный телефон:</label>
        <input type="text" value ={phone} ref = {refPhone} onChange ={changeHandlerPhone}/>
      </div>
      <div className="flex_input_edit">
        <label>Доп. телефон:</label>
        <input type="text" value ={dopPhone} ref = {refDopPhone} onChange ={changeHandlerDopPhone}/>
      </div>
      <div className="flex_input_edit">
        <label>Discord:</label>
        <input type="text" value ={dis} ref = {refDis} onChange ={changeHandlerDis}/>
      </div>
      <div className="flex_input_edit">
        <label>Личный сайт:</label>
        <input type="text" value ={webSite} ref = {refWebSite} onChange ={changeHandlerWebSite}/>
      </div>
      <div id="polosa_edit" class="polosa_bottom_about_me"></div>
      <div onClick={changeView}>
        <button className="bt_save_edit" onClick ={updProfile2}>Сохранить</button>
      </div>
    </div>
  );
};

export default Contact_page;
