import React, { useEffect } from "react";
import "../Edit_page.css";
import * as axios from "axios";
import { useState } from "react";
import Header_edit_page_education from "../header_edit_page/Header_edit_page_education";

const Education_page = (props) => {
  let [view, setView] = useState(false);
  function changeView() {
    if (view == false) {
      setView(true);
    }
  }

  let [country, setCountry] = useState("");

  let [city, setCity] = useState("");
  let [school, setSchool] = useState("");
  let [startedEducation, setEducation] = useState("");
  let [endedEducation, setendedEducation] = useState("");
  let [graduation, setgraduation] = useState("");
  let [klass, setklass] = useState("");
  let [specialization, setspecialization] = useState("");
  function getProfileForEditt() {
    axios
      .get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.id}`)
      .then((response) => {
        setCountry(response.data.country);
        setCity(response.data.city);
        setSchool(response.data.school);
        setEducation(response.data.startedEducation);
        setendedEducation(response.data.endedEducation);
        setgraduation(response.data.graduation);
        setklass(response.data.klass);
        setspecialization(response.data.specialization);
      });
  }

  useEffect(() => {
    getProfileForEditt();
  }, [props.id]);

  let refCountry = React.createRef();
  let refCity = React.createRef();
  let refPhone = React.createRef();
  let refDopPhone = React.createRef();
  let refDis = React.createRef();
  let refWebSite = React.createRef();
  let refWebSit = React.createRef();
  let refWebSi = React.createRef();
 

  function changeHandlerCountry(e) {
    setCountry(e.currentTarget.value);
  }
  function changeHandlerCity(e) {
    setCity(e.currentTarget.value);
  }
  function changeHandlerPhone(e) {
    setSchool(e.currentTarget.value);
  }
  function changeHandlerPhones(e) {
    setEducation(e.currentTarget.value);
  }
  function changeHandlerDis(e) {
    setendedEducation(e.currentTarget.value);
  }
  function changeHandlerWebSite(e) {
    setgraduation(e.currentTarget.value);
  }
  function changeHandlerWebSit(e) {
    setklass(e.currentTarget.value);
  }
  function changeHandlerWebSi(e) {
    setspecialization(e.currentTarget.value);
  }

  function updProfile4() {
    props.updateProfile4(
      props.id,
      refCountry.current.value,
      refCity.current.value,
      refPhone.current.value,
      refDopPhone.current.value,
      refDis.current.value,
      refWebSite.current.value,
      refWebSit.current.value,
      refWebSi.current.value 
    );
  }

  return (
    <div id="wrapper_career_page_education" className="wrapper_edit_page">
      <Header_edit_page_education />
      {view && (
        <div className="wrapper_saved_edit">
          <img src="https://vk.com/images/icons/msg_ok.png?1" alt="" />
          <div className="text_saved_edit">
            <span className="sp_changes_saved">Изменения сохранены.</span>
            <span className="sp_changes_will_see">
              Новые данные будут отражены на Вашей странице.
            </span>
          </div>
        </div>
      )}
      <div className="flex_input_edit">
        <label>Страна:</label>
        <input value={country} type="text" onChange ={changeHandlerCountry} ref ={refCountry}/>
      </div>
      <div className="flex_input_edit">
        <label>Город:</label>
        <input type="text" value={city} onChange ={changeHandlerCity} ref ={refCity}/>
      </div>
      <div className="flex_input_edit">
        <label>Школа:</label>
        <input type="text" value={school} onChange ={changeHandlerPhone} ref ={refPhone}/>
      </div>
      <div className="flex_input_edit">
        <label>ВУЗ:</label>
        <input type="text" value={school} onChange =''/>
      </div>
      <div className="flex_input_edit">
        <label>Год начала обучения:</label>
        <input type="text" value={startedEducation} onChange ={changeHandlerPhones} ref ={refDopPhone}/>
      </div>
      <div className="flex_input_edit">
        <label>Год окончания обучения:</label>
        <input type="text" value={endedEducation} onChange ={changeHandlerDis} ref ={refDis}/>
      </div>
      <div className="flex_input_edit">
        <label>Дата выпуска:</label>
        <input type="text" value={graduation} onChange ={changeHandlerWebSite} ref ={refWebSite}/>
      </div>
      <div className="flex_input_edit">
        <label>Класс:</label>
        <input type="text" value={klass} onChange ={changeHandlerWebSit} ref ={refWebSit}/>
      </div>
      <div className="flex_input_edit">
        <label>Специализация:</label>
        <input type="text" value={specialization} onChange ={changeHandlerWebSi} ref ={refWebSi} />
      </div>

      <div id="polosa_edit" class="polosa_bottom_about_me"></div>
      <div onClick={changeView}>
        <button onClick={updProfile4} className="bt_save_edit">
          Сохранить
        </button>
      </div>
    </div>
  );
};

export default Education_page;
