import React from "react";
import "./profile.css";
import Left_side_profile from "./Left_side_profile/Left_side_profile";
import Right_side_profile from "./Right_side_profile/Right_side_profile";
import { Redirect, withRouter } from "react-router-dom";

const My_profile = (props) => {
  
  if (props.isAuth == false){
   return  <Redirect to= {'/auth'} />
  }
  return (
    <section className="Profile">
      <Left_side_profile myUserId ={props.userId} />
      <Right_side_profile myUserId ={props.userId} userId ={props.match.params.userId}/>
    </section>
  );
};
let withRouterProfile = withRouter(My_profile)

export default withRouterProfile;
