import React, { useState } from "react";
import { useEffect } from "react";
import "../Friends.css";
import * as axios from "axios";
import { NavLink } from "react-router-dom";
const User = (props) => {
  let [src, setSrc] = useState("");
  let [id_go, setId_go] = useState('')
  async function fds(e) {
      
    let photoId = await axios.get(
      `https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.id}`
    );
    if(props.myUserId == props.id){
        setId_go('ib' + props.myUserId)
    } 
    if (props.myUserId != props.id){
        setId_go('id' + props.id)
    }
    if (photoId.data.photo == "") {
     
      setSrc("https://vk.com/images/camera_100.png?ava=1");
    } else {
       
      axios
        .get(
          `https://dry-headland-66248.herokuapp.com/api/upload/${photoId.data.photo}`
        )
        .then((response) => {
          setSrc("data:image/jpeg;base64," + response.data.data);
        });
    }
  }
  useEffect(() => {
    fds();
  }, []);
  return (
    <div className="kjs">
      <div className="flex_user_about">
        <NavLink to ={id_go}><img src={src} alt="" /></NavLink>
        <div className="text_of_flex_user_about">
          <NavLink to = {id_go} ><span><span className ='sp_toch_user1'>{props.name + " " + props.surName}</span></span></NavLink>
          <span><span className ='sp_toch_user2'>Написать сообщение</span></span>
        </div>
      </div>
    </div>
  );
};

export default User;
