import React, { useState, useEffect } from "react";

import * as axios from "axios";
import User from "./User";



const Users = (props) => {
  let [usersOfVkontukte, setUsers] =useState([])
  function getUsers() {
    axios.get('https://dry-headland-66248.herokuapp.com/api/users').then(response=>{
      setUsers(response.data)
  })
  }
 
  

  let UsersOf = usersOfVkontukte.map((p) => (
    <User myUserId= {props.myUserId} id ={p._id} surName = {p.surname} name = {p.name} />
  ));

  useEffect(() =>{getUsers()}, [])
  return (
    <div className="kj">
        {UsersOf}
    </div>
  );
};

export default Users;
