import React, { useState, createRef, useEffect } from "react";
import { getName } from "../../../store/profile-reduser";

import { connect } from "react-redux";
import Header_about_me from "./Header_about_me";
import { useContext } from "react";
import { AuthContext } from "../../../context/Auth-contex";
import * as axios from "axios";

const Header_about_me_cont = (props) => {
  let auth = useContext(AuthContext)
 let [name, setName ] = useState('')
 function getNime() {
    axios.get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.userId}`).then(response=>{
      setName(response.data.name)
    })
 }
  useEffect(() => {getNime()}, []);
  return <Header_about_me id= {auth.userId}  name={name} />;
};

let mapStateToProps = (state) => {
  return {
    name: state.Profile.name,
  };
};

let mapDispatchToProps = {
  getName
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Header_about_me_cont);
