import React, { useState, useContext } from "react";

import {getGender, getRelationship, getWith, getDateBirth, getHomeTown, getLanguages} from '../../../../../store/profile-reduser'

import About_me_info from "./About_me_info";


import { connect } from "react-redux";
import { AuthContext } from "../../../../../context/Auth-contex";
import { useEffect } from "react";
import { withRouter } from "react-router-dom";





const  About_me_cont = (props) => {
  let auth = useContext(AuthContext)
  useEffect(() =>{ props.getGender(props.match.params.userId); props.getRelationship(props.match.params.userId); props.getWith(props.match.params.userId); props.getDateBirth(props.match.params.userId); props.getHomeTown(props.match.params.userId); props.getLanguages(props.match.params.userId) }, [])
  
   
  return <About_me_info id ={props.match.params.userId} gender = {props.gender} relationship ={props.relationship} With = {props.With} dateBirth ={props.dateBirth} homeTown = {props.homeTown} languages= {props.languages}/>;

}
let mapStateToProps = (state) =>{
 return{
    gender: state.Profile.gender,
  relationship: state.Profile.relationship,
  With: state.Profile.With,
  dateBirth: state.Profile.dateBirth,
  homeTown: state.Profile.homeTown,
  languages: state.Profile.languages,
}
}
let mapDispatchToProps = {
    getGender,
    getRelationship,
    getWith,
    getDateBirth,
    getHomeTown,
    getLanguages
}
let withRouterProfile = withRouter(About_me_cont)

export default connect(mapStateToProps, mapDispatchToProps)(withRouterProfile);
