import * as axios from "axios";

const instance = axios.create({
  baseURL: "https://dry-headland-66248.herokuapp.com/api/",
});


export const ProfileAPI = {
    getAPIProf(id){
        return instance.get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${id}`)
    },
    updateAPIStatus(id, status, photo) {
      return instance.put(`Ttatus/${id}`, { status: status });
    },
    updateAPIProfile(id, name, surName, gender, relationship, With, dateBirth, homeTown, languages) {
      return instance.put(`Ttatus/${id}`, {name:name, surName:surName, gender:gender, relationship:relationship, With:With, dateBirth:dateBirth, homeTown:homeTown, languages:languages });
    },
    updateAPIProfile2(id, country, city, phone, dopPhone, dis, webSite) {
      return instance.put(`Ttatus/${id}`, {country:country, city:city, phone:phone, dopPhone:dopPhone, dis:dis, webSite:webSite });
    },
    updateAPIProfile3(id, deals, interests, favMusik, favFilms, favTVShows, favBooks, favGames, favQuotes, AboutMe) {
      return instance.put(`Ttatus/${id}`, { deals:deals, interests:interests, favMusik:favMusik, favFilms:favFilms, favTVShows:favTVShows, favBooks:favBooks, favGames:favGames, favQuotes:favQuotes, AboutMe:AboutMe });
    },
    updateAPIProfile4(id, country,city, school, startedEducation, endedEducation, graduation, klass, specialization) {
      return instance.put(`Ttatus/${id}`, { school:school, country:country, city:city, startedEducation:startedEducation, endedEducation:endedEducation, graduation:graduation, klass:klass, specialization:specialization });
    },
    updateAPIProfile5(id,placeWork, country, city,  startedWork, endedWork, PlaceInWork) {
      return instance.put(`Ttatus/${id}`, {placeWork:placeWork, country:country, city:city,  startedWork:startedWork, endedWork:endedWork, PlaceInWork:PlaceInWork });
    },
    updateAPIProfile6(id, country, parthOfmillitary, startedMillitary, endedMillitary) {
      return instance.put(`Ttatus/${id}`, {country:country, parthOfmillitary:parthOfmillitary, startedMillitary:startedMillitary, endedMillitary:endedMillitary });
    },
    updateAPIProfile7(id, politCoordinats, religion, favInLife, favInPeople, attitudeToSmoking, attitudeToAlcohol, InspiredBy) {
      return instance.put(`Ttatus/${id}`, { politCoordinats:politCoordinats, religion:religion, favInLife:favInLife, favInPeople:favInPeople, attitudeToSmoking:attitudeToSmoking, attitudeToAlcohol:attitudeToAlcohol, InspiredBy:InspiredBy });
    },
    getAPIPosts(userId){
      return instance.get(`Posts/${userId}`)
    },
    sendAPIPost(userId, title, date, liked, count, userrId){
      return instance.post(`Posts/${userId}`, {title: title, date: date, liked:false, countLikes:null, userId:userrId})
    },
    deleteAPIPost(id, title){
      return instance.post(`Posts/del/${id}`, {title:title});
    },
    getAPIPhoto(){
      return instance.get(`upload/5f32aa84e6ae5c00172300a0`)
    },
    setAPILikes(id, visLike, likes, title, date){
      return instance.patch(`Posts/${id}`,{liked: visLike, countLikes: likes, title:title, date:date});
    }
}