import React, { useEffect, useState } from "react";
import * as axios from "axios";
import "./Profile_about_page.css";

const Profile_about_page = (props) => {
  let [countFriend, setCountFriend] = useState([])
  function getFriendss(){
    axios.get(`https://dry-headland-66248.herokuapp.com/api/Friends/${props.userId}`).then(response =>{
      setCountFriend(response.data.friends.length)
    })
  }
  useEffect(() => {
    getFriendss();
  }, [props.userId]);
  return (
    <div className="flex_about_me_info">
      <div className="subcribers_about_info_me">
        <span className="mained_about_info_me">0</span>
        <span className="not_mained_about_info_me1">подписчика</span>
      </div>
      <div className="friends_about_info_me">
        <span className="mained_about_info_me">{countFriend}</span>
        <span className="not_mained_about_info_me2">Друзей</span>
      </div>
      <div className="photos_about_info_me">
        <span className="mained_about_info_me">1</span>
        <span className="not_mained_about_info_me3">фотография</span>
      </div>
      <div className="audios_about_info_me">
        <span className="mained_about_info_me">0</span>
        <span className="not_mained_about_info_me4">аудиозаписей</span>
      </div>
    </div>
  );
};
export default Profile_about_page;
