import React, { useState, useEffect } from "react";
import * as axios from "axios";
import "./Navbar_edit_page.css";
import { NavLink } from "react-router-dom";

const Navbar_edit_page = (props) => {
  let [src, setSrc] = useState("");
  async function fds(e) {
    let photoId = await axios.get(
      `https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.id}`
    );
    if (photoId.data.photo == "") {
      setSrc("https://vk.com/images/camera_100.png?ava=1");
    } else {
      axios
        .get(
          `https://dry-headland-66248.herokuapp.com/api/upload/${photoId.data.photo}`
        )
        .then(async (response) => {
          setSrc("data:image/jpeg;base64," + response.data.data);
        });
    }
  }

  useEffect(() => {
    fds();
  }, []);
  return (
    <div className="wrapper_navbar_edit">
      <div className="navbar_edit_header">
        <NavLink to={'ib' +props.id}>
          <div className="header_navbar_edit_about_user">
            <img src={src} alt="" />
            <span>{props.name}</span>
            <span>{props.surName}</span>
          </div>
        </NavLink>
      </div>
      <div className="polosa_edit_header"></div>
      <div className="wrapper_navling_edit_navber">
        <NavLink to="/main_edit">Основное</NavLink>
        <NavLink to="/contacts_edit">Контакты</NavLink>
        <NavLink to="/interests_edit">Интересы</NavLink>
        <NavLink to="/education_edit">Образование</NavLink>
        <NavLink to="/career_edit">Карьера</NavLink>
        <NavLink to="/millitary_edit">Военная служба</NavLink>
        <NavLink to="/personal_edit">Жизненная позиция</NavLink>
      </div>
    </div>
  );
};

export default Navbar_edit_page;
