import React from "react";
import "./Not_reveald_about_me_info.css";
import { useState } from "react";
import { useEffect } from "react";
import * as axios from "axios";

const Not_reveald_about_me_info = (props) => {
  let [viewDate, setViewDate] = useState(true)
  let [school, setSchool] = useState(true)
  let [city, setSity] = useState(true)
  let [viewSchool, setViewSchool] = useState(true)
  let [viewCity, setViewCity] = useState(true)
  let [viewSite, setViewSite] = useState(true)
  let [site, setSite] = useState(true)
  function changeViewDate(){
    if (props.dateBirth.length < 1){
      setViewDate(false)
    } else {
      setViewDate(true)
    }
  }
  let [viewRel, setViewRel] = useState(true)
  function changeViewRel(){
    if (props.relationship.length < 1){
      setViewRel(false)
    } else {
      setViewRel(true)
    }
  }
  let [viewWith, setViewWith] = useState(true)
  function changeViewWith(){
    if (props.With.length < 1){
      setViewWith(false)
    } else {
      setViewWith(true)
    }
  }
  function getProfileForEditt() {
    axios
      .get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.id}`)
      .then((response) => {
        setSchool(response.data.school)
        setSity(response.data.city)
        setSite(response.data.webSite)
        if (response.data.school.length < 1){
          setViewSchool(false)
        } else {
          setViewSchool(true)
        }
        if (response.data.city.length < 1){
          setViewCity(false)
        } else {
          setViewCity(true)
        }
        if (response.data.webSite.length < 1){
          setViewSite(false)
        } else {
          setViewSite(true)
        }
      });
  }

  
  useEffect(() =>{getProfileForEditt(); changeViewDate(); changeViewRel(); changeViewWith()}, [props.id])
  return (
    <>
      <div className="date_profile_flex">
        <div className="span_mained_date_profile">
          {viewDate && <label className="span_fitst_about_mained" id="birh_1">
            День рождения:
          </label>}
          {viewCity && <label className="span_fitst_about_mained" id="birh_2">
            Город:
          </label>}
          {viewRel && <label className="span_fitst_about_mained" id="birh_3">
            Семейное положение:
          </label>}
          {viewWith && <label className="span_fitst_about_mained" id="birh_3">
            С кем:
          </label>}
          {viewSchool && <label className="span_fitst_about_mained" id="birh_4">
            Место учёбы:
          </label>}
          {viewSite && <label className="span_fitst_about_mained" id="birh_2">
            Сайт:
          </label>}
        </div>
        <div className="span_not_mained_date_profile">
          {viewDate && <div className="span_fitst_about_not_mained">
            <span>{props.dateBirth}</span>
          </div>}
          {viewCity && <div className="span_fitst_about_not_mained">
            <span>{city}</span>
          </div>}
           {viewRel && <div className="span_fitst_about_not_mained">
            <span>{props.relationship}</span>
          </div>}
          {viewWith && <div className="span_fitst_about_not_mained">
            <span>{props.With}</span>
          </div>}
          {viewSchool && <div className="span_fitst_about_not_mained">
            <span>{school}</span>
          </div>}
          {viewSite && <div className="span_fitst_about_not_mained">
            <span>http://{site}</span>
          </div>}
        </div>
      </div>
    </>
  );
};
export default Not_reveald_about_me_info;
