import React from "react";
import "./App.css";
import Header from "./Components/Header/Header";
import Sidebar from "./Components/Sidebar/Sidebar";
import Auth_page from "./Components/AuthPage/AuthPage";
import My_profile from "./Components/My_profile/My_profile";
import { HashRouter, Route, Redirect, BrowserRouter } from "react-router-dom";
import { useAuth } from "./hooks/auth-hook";
import { AuthContext } from "./context/Auth-contex";
import Edit_page_const from "./Components/Edit_page/Edit_page_const";
import Friends_main from "./Components/Friends/Friends";
import Zaglu from "./Components/zaglu";




function App() {
  const { token, login, logout, userId } = useAuth();
  const isAuthenticated = !!token;
  return (
    <AuthContext.Provider
      value={{ token, login, logout, userId, isAuthenticated }}
    >
      <HashRouter>
      
        <div className="wrapper_main">
          <Zaglu isAuth={isAuthenticated} userId={userId}/>
          <Header userId={userId} isAuth={isAuthenticated} />
          <div className="wrapper_flex_main">
            <Sidebar userId={userId} isAuth={isAuthenticated} />
            <Route
              path="/id:userId"
              render={() => (
                <My_profile userId={userId}  isAuth={isAuthenticated} />
              )}
            ></Route>
            <Route
              path="/friends"
              render={() => (
                <Friends_main userId={userId} isAuth={isAuthenticated} />
              )}
            ></Route>
            <Route path = '/main_edit' render ={(() =>  <Edit_page_const />)}></Route>  
            <Route path = '/contacts_edit' render ={(() =>  <Edit_page_const />)}></Route>  
            <Route path = '/interests_edit' render ={(() =>  <Edit_page_const />)}></Route>  
            <Route path = '/education_edit' render ={(() =>  <Edit_page_const />)}></Route>  
            <Route path = '/career_edit' render ={(() =>  <Edit_page_const />)}></Route>  
            <Route path = '/millitary_edit' render ={(() =>  <Edit_page_const />)}></Route>  
            <Route path = '/personal_edit' render ={(() =>  <Edit_page_const />)}></Route>  
            {!isAuthenticated && (
              <Route path="/auth" render={() => <Auth_page />}></Route>
            )}
          </div>
        </div>
      
      </HashRouter>
    </AuthContext.Provider>
  );
}

export default App;
