import React, { useEffect } from "react";
import "../Edit_page.css";
import * as axios from "axios";
import { useState } from "react";
import Header_edit_page_career from "../header_edit_page/Header_edit_page_career";

const Career_page = (props) => {
  let [view, setView] = useState(false);
  function changeView() {
    if (view == false) {
      setView(true);
    }
  }
  let [country, setCountry] = useState("");

  let [city, setCity] = useState("");
  let [placeWork, setplaceWork] = useState("");
  let [startedWork, setWork] = useState("");
  let [endedWork, setendedWork] = useState("");
  let [PlaceInWork, setPlaceInWork] = useState("");

  function getProfileForEditt() {
    axios
      .get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.id}`)
      .then((response) => {
        setplaceWork(response.data.placeWork);
        setCountry(response.data.country);
        setCity(response.data.city);
        setWork(response.data.startedWork);
        setendedWork(response.data.endedWork);
        setPlaceInWork(response.data.PlaceInWork);
      });
  }

  useEffect(() => {
    getProfileForEditt();
  }, [props.id]);

  let refCountry = React.createRef();
  let refCity = React.createRef();
  let refPhone = React.createRef();
  let refDopPhone = React.createRef();
  let refDis = React.createRef();
  let refWebSite = React.createRef();

 

  function changeHandlerCountry(e) {
    setplaceWork(e.currentTarget.value);
  }
  function changeHandlerCity(e) {
    setCountry(e.currentTarget.value);
  }
  function changeHandlerPhone(e) {
    setCity(e.currentTarget.value);
  }
  function changeHandlerPhones(e) {
    setWork(e.currentTarget.value);
  }
  function changeHandlerDis(e) {
    setendedWork(e.currentTarget.value);
  }
  function changeHandlerWebSite(e) {
    setPlaceInWork(e.currentTarget.value);
  }
  



  function updProfile5() {
    props.updateProfile5(
      props.id,
      refCountry.current.value,
      refCity.current.value,
      refPhone.current.value,
      refDopPhone.current.value,
      refDis.current.value,
      refWebSite.current.value,
    );
  }
  return (
    <div id = 'wrapper_career_page_edit' className="wrapper_edit_page">
      <Header_edit_page_career />
      {view && (
        <div className="wrapper_saved_edit">
          <img src="https://vk.com/images/icons/msg_ok.png?1" alt="" />
          <div className="text_saved_edit">
            <span className="sp_changes_saved">Изменения сохранены.</span>
            <span className="sp_changes_will_see">
              Новые данные будут отражены на Вашей странице.
            </span>
          </div>
        </div>
      )}
     <div className="flex_input_edit">
        <label>Место работы:</label>
        <input type="text" value ={placeWork} onChange ={changeHandlerCountry} ref = {refCountry}/>
      </div>
      <div className="flex_input_edit">
        <label>Страна:</label>
        <input type="text" value ={country} onChange ={changeHandlerCity} ref = {refCity}/>
      </div>
      <div className="flex_input_edit">
        <label>Город:</label>
        <input type="text" value ={city} onChange ={changeHandlerPhone} ref = {refPhone}/>
      </div>
      <div className="flex_input_edit">
        <label>Год начала работы:</label>
        <input type="text" value ={startedWork} onChange ={changeHandlerPhones} ref = {refDopPhone}/>
      </div>
      <div className="flex_input_edit">
        <label>Год окончания работы:</label>
        <input type="text" value ={endedWork} onChange ={changeHandlerDis} ref = {refDis}/>
      </div>
      <div className="flex_input_edit">
        <label>Должность:</label>
        <input type="text" value ={PlaceInWork} onChange ={changeHandlerWebSite} ref = {refWebSite}/>
      </div>
     

      <div id="polosa_edit" class="polosa_bottom_about_me"></div>
      <div onClick={changeView}>
        <button onClick = {updProfile5} className="bt_save_edit">Сохранить</button>
      </div>
    </div>
  );
};

export default Career_page;
