import React from "react";
import "./Edit_page.css";

import Main_page from "./Pages_of_edit/Main_page";
import Contact_page from "./Pages_of_edit/Contact_page";
import Navbar_edit_page from "./Navbar_edit_page/Navbar_edit_page";

import { Switch, Route } from "react-router-dom";
import Interests_page from "./Pages_of_edit/Interests_page";
import Career_page from "./Pages_of_edit/Career_page";
import Millitary_page from "./Pages_of_edit/Millitary_page";
import Personal_page from "./Pages_of_edit/Personal_page";
import Education_page from "./Pages_of_edit/Education_page";

const Edit_page = (props) => {
  return (
    <>
      <Switch>
        <Route path="/main_edit" exact>
          <Main_page
            gender={props.gender}
            relationship={props.relationship}
            With={props.With}
            dateBirth={props.dateBirth}
            homeTown={props.homeTown}
            languages={props.languages}
            id={props.id}
            updateProfile={props.updateProfile}
            surName={props.surName}
            name={props.name}
          />
        </Route>
        <Route path="/contacts_edit" exact>
          <Contact_page
            id={props.id}
            updateProfile2={props.updateProfile2}
        
          />
        </Route>
        <Route path="/interests_edit" exact>
          <Interests_page
            id={props.id}
            updateProfile3={props.updateProfile3}
          
          />
        </Route>
        <Route path="/education_edit" exact>
          <Education_page
            id={props.id}
            updateProfile4={props.updateProfile4}
       
          />
        </Route>
        <Route path="/career_edit" exact>
          <Career_page
            id={props.id}
            updateProfile5={props.updateProfile5}
       
          />
        </Route>
        <Route path="/millitary_edit" exact>
          <Millitary_page
            id={props.id}
            updateProfile6={props.updateProfile6}
          
          />
        </Route>
        <Route path="/personal_edit" exact>
          <Personal_page
            id={props.id}
            updateProfile7={props.updateProfile7}
            
          />
        </Route>
      </Switch>
      <Navbar_edit_page
        id={props.id}
        name={props.name}
        surName={props.surName}
      />
    </>
  );
};

export default Edit_page;
