import React, { useState } from "react";
import "./Reaveald_about_me_info.css";
import { useEffect } from "react";
import * as axios from "axios";

const Reaveald_about_me_info = (props) => {
  let [viewHome, setViewHome] = useState(true);
  function changeViewHome() {
    if (props.homeTown.length < 1) {
      setViewHome(false);
    } else {
      setViewHome(true);
    }
  }
  let [viewLang, setViewLang] = useState(true);
  function changeViewLang() {
    if (props.languages.length < 1) {
      setViewLang(false);
    } else {
      setViewLang(true);
    }
  }
  let [school, setSchool] = useState(true);
  let [startedEducation, setEducation] = useState(true);
  let [endedEducation, setEndedEducation] = useState(true);
  let [graduation, setGraduation] = useState(true);
  let [klass, setKlass] = useState(true);
  let [specialization, setSpecialization] = useState(true);
  let [city, setSity] = useState(true);
  let [country, setCountry] = useState(true);
  let [viewSchool, setViewSchool] = useState(true);
  let [viewCity, setViewCity] = useState(true);

  let [placeWork, setPlaceWork] = useState(true);

  let [startedWork, setStartedWork] = useState(true);
  let [endedWork, setEndedWork] = useState(true);
  let [PlaceInWork, setPlaceInWork] = useState(true);

  let [parthOfmillitary, setParthOfmillitary] = useState(true);
  let [startedMillitary, setStartedMillitary] = useState(true);
  let [endedMillitary, setEndedMillitary] = useState(true);

  let [politCoordinats, setPolitCoordinats] = useState(true);
  let [religion, setReligion] = useState(true);
  let [favInLife, setFavInLife] = useState(true);
  let [favInPeople, setFavInPeople] = useState(true);
  let [attitudeToSmoking, setAttitudeToSmoking] = useState(true);
  let [attitudeToAlcohol, setAttitudeToAlcohol] = useState(true);
  let [InspiredBy, setInspiredBy] = useState(true);

  let [deals, setDeals] = useState(true);
  let [interests, setInterests] = useState(true);
  let [favMusik, setFavMusik] = useState(true);
  let [favFilms, setFavFilms] = useState(true);
  let [favTVShows, setFavTVShows] = useState(true);
  let [favBooks, setFavBooks] = useState(true);
  let [favGames, setFavGames] = useState(true);
  let [favQuotes, setFavQuotes] = useState(true);
  let [AboutMe, setAboutMe] = useState(true);

  let [phone, setPhone] = useState(true);
  let [dopPhone, setDopPhone] = useState(true);
  let [dis, setDis] = useState(true);

  let [viewPlaceWork, setViewPlaceWork] = useState(true);
  let [viewParthOfmillitary, setViewParthOfmillitary] = useState(true);
  let [viewPhone, setviewPhone] = useState(true);
  let [viewDopPhone, setviewDopPhone] = useState(true);
  let [viewDis, setviewDis] = useState(true);

  let [viewPolitCoordinats, setviewPolitCoordinats] = useState(true);


  let [viewDeals, setviewDeals] = useState(true);
  let [viewInterests, setviewInterests] = useState(true);
  let [viewFavMusik, setViewFavMusik] = useState(true);
  let [viewFavFilms, setviewFavFilms] = useState(true);
  let [viewFavTVShows, setviewTVShows] = useState(true);
  let [viewFavBooks, setviewFavBooks] = useState(true);
  let [viewFavGames, setviewFavGames] = useState(true);
  let [viewFavQuotes, setvieFavQuotes] = useState(true);
  let [viewAboutMe, setviewAboutMe] = useState(true);
  function getProfileForEditt() {
    axios
      .get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.id}`)
      .then((response) => {
        setSchool(response.data.school);
        setEducation(response.data.startedEducation);
        setEndedEducation(response.data.endedEducation);
        setGraduation(response.data.graduation);
        setKlass(response.data.klass);
        setSpecialization(response.data.specialization);
        setSity(response.data.city);
        setCountry(response.data.country);

        setPlaceWork(response.data.placeWork);
        setStartedWork(response.data.startedWork);
        setEndedWork(response.data.endedWork);
        setPlaceInWork(response.data.PlaceInWork);

        setParthOfmillitary(response.data.parthOfmillitary);
        setStartedMillitary(response.data.startedMillitary);
        setEndedMillitary(response.data.endedMillitary);

        setPhone(response.data.phone);
        setDopPhone(response.data.dopPhone);
        setDis(response.data.dis);

        setPolitCoordinats(response.data.politCoordinats);
        setReligion(response.data.religion);
        setFavInLife(response.data.favInLife);
        setFavInPeople(response.data.favInPeople);
        setAttitudeToSmoking(response.data.attitudeToSmoking);
        setAttitudeToAlcohol(response.data.attitudeToAlcohol);
        setInspiredBy(response.data.InspiredBy);

        setDeals(response.data.deals);
        setInterests(response.data.interests);
        setFavMusik(response.data.favMusik);
        setFavFilms(response.data.favFilms);
        setFavTVShows(response.data.favTVShows);
        setFavBooks(response.data.favBooks);
        setFavGames(response.data.favGames);
        setFavQuotes(response.data.favQuotes);
        setAboutMe(response.data.AboutMe);

        if (response.data.politCoordinats.length < 1) {
          setviewPolitCoordinats(false);
        } else {
          setviewPolitCoordinats(true);
        }
        if (response.data.school.length < 1) {
          setViewSchool(false);
        } else {
          setViewSchool(true);
        }
        if (response.data.city.length < 1) {
          setViewCity(false);
        } else {
          setViewCity(true);
        }
        if (response.data.placeWork.length < 1) {
          setViewPlaceWork(false);
        } else {
          setViewPlaceWork(true);
        }
        if (response.data.parthOfmillitary.length < 1) {
          setViewParthOfmillitary(false);
        } else {
          setViewParthOfmillitary(true);
        }
        if (response.data.phone.length < 1) {
          setviewPhone(false);
        } else {
          setviewPhone(true);
        }
        if (response.data.dopPhone.length < 1) {
          setviewDopPhone(false);
        } else {
          setviewDopPhone(true);
        }
        if (response.data.dis.length < 1) {
          setviewDis(false);
        } else {
          setviewDis(true);
        }
        if (response.data.deals.length < 1) {
          setviewDeals(false);
        } else {
          setviewDeals(true);
        }
        if (response.data.interests.length < 1) {
          setviewInterests(false);
        } else {
          setviewInterests(true);
        }
        if (response.data.favMusik.length < 1) {
          setViewFavMusik(false);
        } else {
          setViewFavMusik(true);
        }
        if (response.data.favFilms.length < 1) {
          setviewFavFilms(false);
        } else {
          setviewFavFilms(true);
        }
        if (response.data.favTVShows.length < 1) {
          setviewTVShows(false);
        } else {
          setviewTVShows(true);
        }
        if (response.data.favBooks.length < 1) {
          setviewFavBooks(false);
        } else {
          setviewFavBooks(true);
        }
        if (response.data.favGames.length < 1) {
          setviewFavGames(false);
        } else {
          setviewFavGames(true);
        }
        if (response.data.favQuotes.length < 1) {
          setvieFavQuotes(false);
        } else {
          setvieFavQuotes(true);
        }
        if (response.data.AboutMe.length < 1) {
          setviewAboutMe(false);
        } else {
          setviewAboutMe(true);
        }
      });
  }

  useEffect(() => {
    getProfileForEditt();
  }, []);

  useEffect(() => {
    getProfileForEditt();
    changeViewHome();
    changeViewLang();
  }, [props.homeTown]);

  return (
    <>
      <span id="bbbbb" className="span_fitst_about_not_mained">
        Основная информация
        <div className="palka_reaveald"></div>
      </span>
      <div className="wrapper_mini_flex_revealead_info">
        <div className="reaveald_first_stolb_1">
          {viewHome && <span>Родной город:</span>}
          {viewLang && <span>Языки:</span>}
        </div>
        <div className="reaveald_second_stolb_1">
          {viewHome && (
            <div className="span_fitst_about_not_mained">
              <span>{props.homeTown}</span>
            </div>
          )}
          {viewLang && (
            <div className="span_fitst_about_not_mained">
              <span>{props.languages}</span>
            </div>
          )}
        </div>
      </div>
      <span className="span_fitst_about_not_mained">
        Контактная информация <div className="palka_reaveald"></div>
      </span>
      <div className="wrapper_mini_flex_revealead_info">
        <div className="reaveald_first_stolb_5">
          {viewPhone && <span>Моб. телефон:</span>}
          {viewDopPhone && <span>Доп. телефон:</span>}
          {viewDis && <span>Discord:</span>}
        </div>
        <div className="reaveald_second_stolb_6">
          <div className="span_fitst_about_not_mained">
            {viewPhone && <span>{phone}</span>}
          </div>
          <div className="span_fitst_about_not_mained">
            {viewDopPhone && <span>{dopPhone}</span>}
          </div>
          <div className="span_fitst_about_not_mained">
            {viewDis && <span>{dis}</span>}
          </div>
        </div>
      </div>
      <span className="span_fitst_about_not_mained">
        Карьера <div className="palka_reaveald"></div>
      </span>
      {viewPlaceWork && (
        <div className="wrapper_mini_flex_revealead_info">
          <div className="reaveald_first_stolb_5">
            <span>Место работы:</span>
          </div>
          <div
            id="reveleald_stolp_last_need"
            className="reaveald_second_stolb_2"
          >
            <div className="span_fitst_about_not_mained">
              <span>{placeWork}</span>
            </div>
            <div>
              <span>
                {city}, {startedWork}–{endedWork}
              </span>
            </div>
            <div>
              <span>{PlaceInWork}</span>
            </div>
          </div>
        </div>
      )}
      <div className="flex_palka_reaveald">
        <span className="span_fitst_about_not_mained">Образование</span>
        <div className="palka_reaveald"></div>
      </div>

      {viewSchool && (
        <div className="wrapper_mini_flex_revealead_info">
          <div className="reaveald_first_stolb_2">
            <span>Школа:</span>
          </div>
          <div className="reaveald_second_stolb_2">
            <div className="span_fitst_about_not_mained">
              <span>
                {school} `{graduation}
              </span>
            </div>
            <div>
              <span>
                {city}, {startedEducation}–{endedEducation} ({klass})
              </span>
            </div>
            <div>
              <span>{specialization}</span>
            </div>
          </div>
        </div>
      )}
      <span className="span_fitst_about_not_mained">
        Военная служба <div className="palka_reaveald"></div>
      </span>
      {viewParthOfmillitary && (
        <div className="wrapper_mini_flex_revealead_info">
          <div className="reaveald_first_stolb_3">
            <span>Войсковая часть:</span>
          </div>
          <div className="reaveald_second_stolb_3">
            <div className="span_fitst_about_not_mained">
              <span>{parthOfmillitary}</span>
            </div>
            <div span_fitst_about_not_mained>
              <span>
                {country}, {startedMillitary}–{endedMillitary}
              </span>
            </div>
          </div>
        </div>
      )}
      <span className="span_fitst_about_not_mained">
        Жизненная позиция <div className="palka_reaveald"></div>
      </span>
      <div className="wrapper_mini_flex_revealead_info">
       {viewPolitCoordinats && <div className="reaveald_first_stolb_4">
          <span>Полит. предпочтения:</span>
          <span>Мировоззрение:</span>
          <span>Главное в жизни:</span>
          <span>Главное в людях:</span>
          <span>Отн. к курению:</span>
          <span>Отн. к алкоголю:</span>
          <span>Вдохновляют:</span>
        </div>}
        <div className="reaveald_second_stolb_4">
          <div className="span_fitst_about_not_mained">
            <span>{politCoordinats}</span>
          </div>
          <div className="span_fitst_about_not_mained">
            <span>{religion}</span>
          </div>
          <div className="span_fitst_about_not_mained">
            <span>{favInLife}</span>
          </div>
          <div className="span_fitst_about_not_mained">
            <span>{favInPeople}</span>
          </div>
          <div className="span_fitst_about_not_mained">
            <span>{attitudeToSmoking}</span>
          </div>
          <div className="span_fitst_about_not_mained">
            <span>{attitudeToAlcohol}</span>
          </div>
          <div className="span_fitst_about_not_mained">
            <span>{InspiredBy}</span>
          </div>
        </div>
      </div>
      <span className="span_fitst_about_not_mained">
        Личная информация <div className="palka_reaveald"></div>
      </span>
      <div className="wrapper_mini_flex_revealead_info">
        <div className="reaveald_first_stolb_5">
          {viewDeals && <span>Деятельность:</span>}
          {viewInterests && <span>Интересы:</span>}
          {viewFavMusik && <span>Любимая музыка:</span>}
          {viewFavFilms && <span>Любимые фильмы:</span>}
          {viewFavTVShows && <span>Любимые телешоу:</span>}
         {viewFavBooks && <span>Любимые книги:</span>}
          {viewFavGames && <span>Любимые игры:</span>}
         {viewFavQuotes && <span>Любимые цитаты:</span>}
         {viewAboutMe && <span>О себе:</span>}
        </div>
        <div className="reaveald_second_stolb_5">
          <div className="span_fitst_about_not_mained">
            <span>{deals}</span>
          </div>
          <div className="span_fitst_about_not_mained">
            <span>{interests}</span>
          </div>
          <div className="span_fitst_about_not_mained">
            <span>{favMusik}</span>
          </div>
          <div className="span_fitst_about_not_mained">
            <span>{favFilms}</span>
          </div>
          <div className="span_fitst_about_not_mained">
            <span>{favTVShows}</span>
          </div>
          <div className="span_fitst_about_not_mained">
            <span>{favBooks}</span>
          </div>
          <div className="span_fitst_about_not_mained">
            <span>{favGames}</span>
          </div>
          <div className="span_fitst_about_not_mained">
            <span>{favQuotes}</span>
          </div>
          <div className="span_fitst_about_not_mained">
            <span>{AboutMe}</span>
          </div>
        </div>
      </div>
    </>
  );
};
export default Reaveald_about_me_info;
