import React, { useEffect } from "react";
import "./Profile_photo.css";
import { useState } from "react";
import * as axios from "axios";
import { NavLink } from "react-router-dom";
import { set } from "mongoose";

const Profile_photo =  (props) => {
  let [selectedFile, setSelectedFile] = useState(null);
  let [src, setSrc] = useState("");
  let [visCh, setVisCh] = useState(true);

  let fileSelectorHandler = async (event) => {
    setSelectedFile(event.target.files[0]);
  };

  let [photos, setPhotos] = useState(props.photoId);
  async function k() {
    let photId = await axios.get(
      `https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.id}`
    );
    setPhotos(photId.data.photo);
  }
  let [viewMy, setViewMy] = useState(false);
  function fff() {
    if (visCh == false) {
      setVisCh(true);
    }
    if (visCh == true) {
      setVisCh(false);
    }
  }
  async function dsf() {
    const id = await axios.get(
      `https://dry-headland-66248.herokuapp.com/api/photo`
    );

    axios.put(
      `https://dry-headland-66248.herokuapp.com/api/photos/${props.id}`,
      { photo: id.data[0]._id }
    );
  }
  async function fd(e) {
    axios.put(
      `https://dry-headland-66248.herokuapp.com/api/photos/${props.id}`,
      { photed: true }
    );
  }
  function getViewMy() {
    if (props.myUserId == props.id) {
      setViewMy(true);
    }
    if (props.myUserId != props.id) {
      setViewMy(false);
    }
  }
  async function fds(e) {
    let photoId = await axios.get(
      `https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.id}`
    );

    const id = await axios.get(
      `https://dry-headland-66248.herokuapp.com/api/photo`
    );

    if (photoId.data.photo == "") {
      setSrc("https://vk.com/images/camera_100.png?ava=1");
      if (photoId.data.photed == true) {
        axios.put(
          `https://dry-headland-66248.herokuapp.com/api/photos/${props.id}`,
          { photo: id.data[0]._id }
        );

        axios.put(
          `https://dry-headland-66248.herokuapp.com/api/photos/${props.id}`,
          { photed: false }
        );
      }
    } else {
      if (photoId.data.photed == true) {
        axios.put(
          `https://dry-headland-66248.herokuapp.com/api/photos/${props.id}`,
          { photo: id.data[0]._id }
        );

        axios.put(
          `https://dry-headland-66248.herokuapp.com/api/photos/${props.id}`,
          { photed: false }
        );
      }
      axios
        .get(
          `https://dry-headland-66248.herokuapp.com/api/upload/${photoId.data.photo}`
        )
        .then(async (response) => {
          setSrc("data:image/jpeg;base64," + response.data.data);
        });
    }
  }
  function addFriend() {
    axios.post(
      `https://dry-headland-66248.herokuapp.com/api/Friends/${props.id}`,
      { userId: props.myUserId }
    );
    axios.post(
      `https://dry-headland-66248.herokuapp.com/api/Friends/${props.myUserId}`,
      { userId: props.id }
    );
  }
  let [viewFriends, setViewFriends] = useState([]);

  async function  getFriendss() {
    await axios
      .get(`https://dry-headland-66248.herokuapp.com/api/Friends/${props.id}`)
      .then( async(response) => {
        return await setViewFriends([...viewFriends, ...response.data.friends]);
      });
  }
  let [viewfinFriends, setViewFinFriends] = useState(true)
 
  let myFriends = viewFriends.map((f) =>   f.my_friends);

 
   function  finViewFriends(){
    if (myFriends.includes(props.myUserId)){
      setViewFinFriends(true)
    } else{
      setViewFinFriends(false)
    }
  }
  
  useEffect(() => {
    getFriendss();
  }, [props.id]);
  useEffect(() => {
    getFriendss();
  }, [addFriend]);
  useEffect(() => {
    fds();
  }, [props.id]);
  useEffect(() => {
    getViewMy();
  }, [props.id]);
  useEffect(() => {
    finViewFriends();
  }, [myFriends]);

   return (
    <div className="profile_photo">
      <img
        onClick={fff}
        src={src || "https://vk.com/images/camera_100.png?ava=1"}
        alt=""
        className="profile_photo_img"
      />

      {viewMy && (
        <div className="flex_profile_photo">
          {!visCh && (
            <form
              className="acd"
              action="https://dry-headland-66248.herokuapp.com/api/upload"
              method="POST"
              encType="multipart/form-data"
            >
              <input onChange={fileSelectorHandler} type="file" name="file" />

              <button onClick={fd}>Загрузите фотогрфаию</button>
            </form>
          )}
          <NavLink className="per_to_edit" to="main_edit">
            <button className="bt1_photo">Редактировать</button>
          </NavLink>
          <button className="bt2_photo">...</button>
        </div>
      )}
      {!viewMy && (
        <>
          <div className="flex_profile_photo" id="flex_profile_photo_dop">
            <button className="bt1_photos" id="send_gifts_icons">
              Написать сообщение{" "}
            </button>
            <div className="palkavverhgifts"></div>
            <img
              src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAsAAAANCAQAAADV/FywAAAAgUlEQVR42p3KQQoBARQG4De5kVJSSo5j4wDKwgEslQ1nmsaoiQNYmRJLPjGDKdl4/+L9ff0RoS1zNBFGSrmuiAiZsSk6TpbmthWX+lqOVg4SA2XFUzd7Z4WLwtXsyRF6FnKlnbVhtd14Xbxb9qlN9osjJHUa7cHf6z84dXsm6p+KOziY2ttZiqEJAAAAAElFTkSuQmCC"
              alt=""
            />
          </div>
          <div className="flex_profile_photo" id="flex_profile_photo_dop">
            {!viewfinFriends && <button onClick={addFriend} className="bt1_photo">
              Добавить в друзья
            </button>}
            {viewfinFriends && <button onClick={addFriend} id ='have_friend_dop' className="bt1_photo">
              У Вас в друзьях
            </button>}
            <button className="bt2_photo">...</button>
          </div>
        </>
      )}
    </div>
  );
};
export default Profile_photo;
