import React, { useState, useEffect } from "react";
import "./Post.css";
import * as axios from "axios";
import { NavLink, withRouter } from "react-router-dom";

const Post = (props) => {
  let [vis, setViss] = useState(true);
  let [likes, setLikess] = useState(props.likesCount);
  let [s, setS] = useState(false);

  let [visLike, setVisLike] = useState(props.liked);
  let [visible, setVisible] = useState(true);

  function likeGlobal() {
    if (visLike == true) {
      setLikess(likes + 1);
      setVisLike(false);
      props.setLikes(props.userId, false, likes, props.info, props.date);
    }
    if (visLike == false) {
      setVisLike(true);
      props.setLikes(props.userId, true, likes, props.info, props.date);
    }

    if (s == false) {
      setS(true);
    }
    if (s == true) {
      setS(false);
    }
  }

  function setVis() {
    if (visible == false) {
      setVisible(true);
    }
    if (visible == true) {
      setVisible(false);
    }
  }
  function set() {
    if (s == false) {
      setS(true);
    }
    if (s == true) {
      setS(false);
    }
  }
  function delP() {
    props.deletePost(props.userId, props.info);
  }
  let [id_go, setId_go] = useState("");
  let [src, setSrc] = useState(props.id);
  let [name, setName] = useState("");
  let [surname, setSurname] = useState("");
  function getNime(idd) {
    axios
      .get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${idd}`)
      .then((response) => {
        setName(response.data.name);
      });
  }
  function getSurName(idd) {
    axios
      .get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${idd}`)
      .then((response) => {
        setSurname(response.data.surname);
      });
  }
  async function fds(e) {
    let photoId = await axios.get(
      `https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.id}`
    );
    getNime(props.id);
    getSurName(props.id);
    if (props.match.params.userId == props.id) {
      setId_go("id" + props.match.params.userId);
    }
    if (props.match.params.userId != props.id) {
      setId_go("id" + props.id);
    }

    if (photoId.data.photo == "") {
      setSrc("https://vk.com/images/camera_100.png?ava=1");
    } 
    if (photoId.data.photo.length > -1) {
      axios
        .get(
          `https://dry-headland-66248.herokuapp.com/api/upload/${photoId.data.photo}`
        )
        .then((response) => {
          setSrc("data:image/jpeg;base64," + response.data.data);
        });
    }
  }

  function viewId() {
    console.log(
      "айди поста:" +
        " " +
        props.id +
        "id моё:" +
        " " +
        props.match.params.userId +
        "айди урла:" +
        " " +
        props.userId +
        "     " +
        id_go
    );
  }

  useEffect(() => {
    props.getPosts(props.userId);
  }, [s]);
  useEffect(() => {
    fds(); 
  }, [props.id]);
  useEffect(() => {
    fds();  
  }, [props.userId]);


  return (
    <div className={(vis && "wrapper_new_post") || "wrapper_new_post_off"}>
      <div className="wrapper_content_new_text">
        <div className="top_new_post">
          <NavLink to={"id" + props.id}>
            <img
              src={src || "https://vk.com/images/camera_100.png?ava=1"}
              alt=""
              onClick={viewId}
            />
          </NavLink>
          <div className="about_sender_post">
            <span className="name_sender_post">
              <NavLink to={"id" + props.id}>
                <span>{name} </span>
                <span>{surname} </span>
              </NavLink>
            </span>

            <span className="date_sender_post">{props.date}</span>
          </div>
          <div className="vis">
            <img
              onClick={setVis}
              className="arrowss"
              src="data:image/svg+xml;charset=utf-8,%3Csvg%20height%3D%2224%22%20viewBox%3D%220%200%2024%2024%22%20width%3D%2224%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cg%20fill%3D%22none%22%20fill-rule%3D%22evenodd%22%3E%3Cpath%20d%3D%22m0%200h24v24h-24z%22%2F%3E%3Cpath%20d%3D%22m6.5%2010%205.5%204.5%205.5-4.5%22%20stroke%3D%22%23c1c5cc%22%20stroke-linecap%3D%22round%22%20stroke-linejoin%3D%22round%22%20stroke-width%3D%221.875%22%2F%3E%3C%2Fg%3E%3C%2Fsvg%3E"
              alt=""
            />
            {!visible && (
              <div className="arrow_menu">
                <div onClick={set} id="shat_hss" className="arrow_menu_zags">
                  <div onClick={delP}>
                    <span>Удалить запись</span>
                  </div>
                </div>
                <div id="shat_hss" className="arrow_menu_zags">
                  <span>Архивировать</span> <span>запись</span>
                </div>
                <div id="shat_hss" className="arrow_menu_zags">
                  <span>Сохранить</span> <span>в закладках</span>
                </div>
                <div className="arrow_menu_zags">
                  <span>Закрепить</span>
                </div>
                <div className="arrow_menu_zags" id="shat_hss">
                  <span className="fdsfs">Выключить</span>
                  <span>комменатрии</span>
                </div>
              </div>
            )}
          </div>
        </div>

        <div className="main_text_post">
          <div>{props.info}</div>
        </div>
        <div className="polosa"></div>
        <div className="about_new_post">
          {(!visLike && (
            <div className="dsa">
              <span onClick={set}>
                <span>
                  <span onClick={likeGlobal}>
                    <img
                      src="data:image/svg+xml;base64,"
                      alt=""
                    />
                  </span>
                </span>
              </span>
              <span className="likes_count">{props.likesCount}</span>
            </div>
          )) || (
            <div className="dsa">
              <span onClick={set}>
                <span>
                  <span onClick={likeGlobal}>
                    <img src="data:image/svg+xml;base64," />
                  </span>
                </span>
              </span>
              <span className="likes_count">{props.likesCount}</span>
            </div>
          )}
          <img
            src="data:image/svg+xml;base64,"
            alt=""
          />
          <img
            src="data:image/svg+xml;base64,"
            alt=""
          />
          <div className="abount_views">
            <img
              src="data:image/svg+xml;base64,"
              alt=""
            />
            <span>0</span>
          </div>
        </div>
      </div>
    </div>
  );
};
let withRouterProfile = withRouter(Post)

export default withRouterProfile;
