import React, { useState, useEffect } from "react";
import "../Edit_page.css";
import * as axios from "axios";
import Header_edit_page_Interests from "../header_edit_page/Header_edit_page_Interests";

const Interests_page = (props) => {
  let [view, setView] = useState(false);
  function changeView() {
    if (view == false) {
      setView(true);
    }
  }
  let [deals, setdeals] = useState("");
  let [interests, setinterests] = useState("");
  let [favMusik, setfavMusik] = useState("");
  let [favFilms, setfavFilms] = useState("");
  let [favTVShows, setfavTVShows] = useState("");
  let [favBooks, setfavBooks] = useState("");
  let [favGames, setfavGames] = useState("");
  let [favQuotes, setfavQuotes] = useState("");
  let [AboutMe, setAboutMe] = useState("");
  function getProfileForEditt() {
    axios
      .get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.id}`)
      .then((response) => {
        setdeals(response.data.deals);
        setinterests(response.data.interests);
        setfavMusik(response.data.favFilms);
        setfavFilms(response.data.favTVShows);
        setfavTVShows(response.data.favBooks);
        setfavBooks(response.data.favGames);
        setfavGames(response.data.favQuotes);
        setfavQuotes(response.data.AboutMe);
        setAboutMe(response.data.AboutMe);
      });
  }

  useEffect(() => {
    getProfileForEditt();
  }, [props.id]);

  function changeHandlerCountry(e) {
    setdeals(e.currentTarget.value);
  }
  function changeHandlerCity(e) {
    setinterests(e.currentTarget.value);
  }
  function changeHandlerPhone(e) {
    setfavMusik(e.currentTarget.value);
  }
  function changeHandlerDopPhone(e) {
    setfavFilms(e.currentTarget.value);
  }
  function changeHandlerDis(e) {
    setfavTVShows(e.currentTarget.value);
  }
  function changeHandlerWebSite(e) {
    setfavBooks(e.currentTarget.value);
  }
  function changeHandlerWebSit(e) {
    setfavGames(e.currentTarget.value);
  }
  function changeHandlerWebSi(e) {
    setfavQuotes(e.currentTarget.value);
  }
  function changeHandlerWebS(e) {
    setAboutMe(e.currentTarget.value);
  }
  let refCountry = React.createRef();
  let refCity = React.createRef();
  let refPhone = React.createRef();
  let refDopPhone = React.createRef();
  let refDis = React.createRef();
  let refWebSite = React.createRef();
  let refWebSited = React.createRef();
  let refWebSit = React.createRef();
  let refWebSi = React.createRef();

  function updProfile3() {
    props.updateProfile3(
      props.id,
      refCountry.current.value,
      refCity.current.value,
      refPhone.current.value,
      refDopPhone.current.value,
      refDis.current.value,
      refWebSite.current.value,
      refWebSited.current.value,
      refWebSit.current.value,
      refWebSi.current.value
    );
  }

  return (
    <div id="wrapper_edit_page_interests_edit" className="wrapper_edit_page">
      <Header_edit_page_Interests />
      {view && (
        <div className="wrapper_saved_edit">
          <img src="https://vk.com/images/icons/msg_ok.png?1" alt="" />
          <div className="text_saved_edit">
            <span className="sp_changes_saved">Изменения сохранены.</span>
            <span className="sp_changes_will_see">
              Новые данные будут отражены на Вашей странице.
            </span>
          </div>
        </div>
      )}
      <div id="div_textarea_interest_page_edit" className="flex_textarea_edit">
        <label for="textarea">Деятельность:</label>
        <textarea
          value={deals}
          id="textarea_interest_page_edit"
          type="text"
          onChange={changeHandlerCountry}
          ref ={refCountry}
        />
      </div>
      <div id="div_textarea_interest_page_edit" className="flex_textarea_edit">
        <label>Интересы:</label>
        <textarea
          id="textarea_interest_page_edit"
          type="text"
          value={interests}
          onChange={changeHandlerCity}
          ref ={refCity}
        />
      </div>
      <div id="div_textarea_interest_page_edit" className="flex_textarea_edit">
        <label>Любимая музыка:</label>
        <textarea
          id="textarea_interest_page_edit"
          type="text"
          value={favMusik}
          onChange={changeHandlerPhone}
          ref ={refPhone} 
        />
      </div>
      <div id="div_textarea_interest_page_edit" className="flex_textarea_edit">
        <label>Любимые фильмы:</label>
        <textarea
          id="textarea_interest_page_edit"
          type="text"
          value={favFilms}
          onChange={changeHandlerDopPhone}
          ref ={refDopPhone}
        />
      </div>
      <div id="div_textarea_interest_page_edit" className="flex_textarea_edit">
        <label>Любимые телешоу:</label>
        <textarea
          id="textarea_interest_page_edit"
          type="text"
          value={favTVShows}
          onChange={changeHandlerDis}
          ref ={refDis}
        />
      </div>
      <div id="div_textarea_interest_page_edit" className="flex_textarea_edit">
        <label>Любимые книги:</label>
        <textarea
          id="textarea_interest_page_edit"
          type="text"
          value={favBooks}
          onChange={changeHandlerWebSite}
          ref ={refWebSite}
        />
      </div>
      <div id="div_textarea_interest_page_edit" className="flex_textarea_edit">
        <label>Любимые игры:</label>
        <textarea
          id="textarea_interest_page_edit"
          type="text"
          value={favGames}
          onChange={changeHandlerWebSit}
          ref ={refWebSited}
        />
      </div>
      <div id="div_textarea_interest_page_edit" className="flex_textarea_edit">
        <label>Любимые цитаты:</label>
        <textarea
          id="textarea_interest_page_edit"
          type="text"
          value={favQuotes}
          onChange={changeHandlerWebSi}
          ref ={refWebSit}
        />
      </div>
      <div id="div_textarea_interest_page_edit" className="flex_textarea_edit">
        <label>О себе:</label>
        <textarea
          id="textarea_interest_page_edit"
          type="text"
          value={AboutMe}
          onChange={changeHandlerWebS}
          ref ={refWebSi}
        />
      </div>
      <div id="polosa_edit" class="polosa_bottom_about_me"></div>
      <div id="div_textarea_interest_page_edit" onClick={changeView}>
        <button onClick={updProfile3} className="bt_save_edit">
          Сохранить
        </button>
      </div>
    </div>
  );
};

export default Interests_page;
