import React, { useEffect } from "react";
import "../Edit_page.css";
import * as axios from "axios";
import { useState } from "react";
import Header_edit_page_millitary from "../header_edit_page/Header_edit_page_millitary";

const Millitary_page = (props) => {
  let [view, setView] = useState(false);
  function changeView() {
    if (view == false) {
      setView(true);
    }
  }
  let [country, setCountry] = useState("");
  let [parthOfmillitary, setplaceWork] = useState("");
  let [startedMillitary, setWork] = useState("");
  let [endedMillitary, setendedWork] = useState("");
 

  function getProfileForEditt() {
    axios
      .get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.id}`)
      .then((response) => {
        setCountry(response.data.country);
        setplaceWork(response.data.parthOfmillitary);
        setWork(response.data.startedMillitary);
        setendedWork(response.data.endedMillitary);
      });
  }

  useEffect(() => {
    getProfileForEditt();
  }, [props.id]);

  let refCountry = React.createRef();
  let refCity = React.createRef();
  let refPhone = React.createRef();
  let refDopPhone = React.createRef();

 

  function changeHandlerCountry(e) {
    setCountry(e.currentTarget.value);
  }
  function changeHandlerCity(e) {
    setplaceWork(e.currentTarget.value);
  }
  function changeHandlerPhone(e) {
    setWork(e.currentTarget.value);
  }
  function changeHandlerPhones(e) {
    setendedWork(e.currentTarget.value);
  }




  function updProfile6() {
    props.updateProfile6(
      props.id,
      refCountry.current.value,
      refCity.current.value,
      refPhone.current.value,
      refDopPhone.current.value,
    );
  }
  return (
    <div id = 'wrapper_millitary_page_edit'  className="wrapper_edit_page">
      <Header_edit_page_millitary />
      {view && (
        <div className="wrapper_saved_edit">
          <img src="https://vk.com/images/icons/msg_ok.png?1" alt="" />
          <div className="text_saved_edit">
            <span className="sp_changes_saved">Изменения сохранены.</span>
            <span className="sp_changes_will_see">
              Новые данные будут отражены на Вашей странице.
            </span>
          </div>
        </div>
      )}
     <div className="flex_input_edit">
        <label>Страна:</label>
        <input type="text" value ={country} onChange ={changeHandlerCountry} ref ={refCountry}/>
      </div>
      <div className="flex_input_edit">
        <label>Войсковая часть:</label>
        <input type="text" value ={parthOfmillitary} onChange ={changeHandlerCity} ref ={refCity}/>
      </div>
      <div className="flex_input_edit">
        <label>Год начала службы:</label>
        <input type="text" value ={startedMillitary} onChange ={changeHandlerPhone} ref ={refPhone}/>
      </div>
      <div className="flex_input_edit">
        <label>Год окончания службы:</label>
        <input type="text" value ={endedMillitary} onChange ={changeHandlerPhones} ref ={refDopPhone}/>
      </div>
      

      <div id="polosa_edit" class="polosa_bottom_about_me"></div>
      <div onClick={changeView}>
        <button onClick ={updProfile6} className="bt_save_edit">Сохранить</button>
      </div>
    </div>
  );
};

export default Millitary_page;
