import React, { useEffect } from "react";
import "../Edit_page.css";
import * as axios from "axios";
import { useState } from "react";
import Header_edit_page_personal from "../header_edit_page/Header_edit_page_personal";

const Personal_page = (props) => {
  let [view, setView] = useState(false);
  function changeView() {
    if (view == false) {
      setView(true);
    }
  }
  function changeView() {
    if (view == false) {
      setView(true);
    }
  }
  let [politCoordinats, setCountry] = useState("");
  let [religion, setplaceWork] = useState("");
  let [favInLife, setWork] = useState("");
  let [favInPeople, setendedWork] = useState("");
  let [attitudeToSmoking, setAttitudeToSm] = useState("");
  let [attitudeToAlcohol, setAttitudeToAl] = useState("");
  let [InspiredBy, setInspired] = useState("");

  function getProfileForEditt() {
    axios
      .get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.id}`)
      .then((response) => {
        setCountry(response.data.politCoordinats);
        setplaceWork(response.data.religion);
        setWork(response.data.favInLife);
        setendedWork(response.data.favInPeople);
        setAttitudeToSm(response.data.attitudeToSmoking);
        setAttitudeToAl(response.data.attitudeToAlcohol);
        setInspired(response.data.InspiredBy);
      });
  }

  useEffect(() => {
    getProfileForEditt();
  }, [props.id]);

  let refCountry = React.createRef();
  let refCity = React.createRef();
  let refPhone = React.createRef();
  let refDopPhone = React.createRef();
  let refDopPhone1 = React.createRef();
  let refDopPhone2 = React.createRef();
  let refDopPhone3 = React.createRef();

  function changeHandlerCountry(e) {
    setCountry(e.currentTarget.value);
  }
  function changeHandlerCity(e) {
    setplaceWork(e.currentTarget.value);
  }
  function changeHandlerPhone(e) {
    setWork(e.currentTarget.value);
  }
  function changeHandlerPhones(e) {
    setendedWork(e.currentTarget.value);
  }
  function changeHandlerPhones1(e) {
    setAttitudeToSm(e.currentTarget.value);
  }
  function changeHandlerPhones2(e) {
    setAttitudeToAl(e.currentTarget.value);
  }
  function changeHandlerPhones3(e) {
    setInspired(e.currentTarget.value);
  }
  function updProfile7() {
    props.updateProfile7(
      props.id,
      refCountry.current.value,
      refCity.current.value,
      refPhone.current.value,
      refDopPhone.current.value,
      refDopPhone1.current.value,
      refDopPhone2.current.value,
      refDopPhone3.current.value,
    );
  }

  return (
    <div id="wrapper_personal_page_edit" className="wrapper_edit_page">
      <Header_edit_page_personal />
      {view && (
        <div className="wrapper_saved_edit">
          <img src="https://vk.com/images/icons/msg_ok.png?1" alt="" />
          <div className="text_saved_edit">
            <span className="sp_changes_saved">Изменения сохранены.</span>
            <span className="sp_changes_will_see">
              Новые данные будут отражены на Вашей странице.
            </span>
          </div>
        </div>
      )}
      <div className="flex_input_edit">
        <label>Полит. предпочтения:</label>
        <select
          type="selector"
          value={politCoordinats}
          onChange={changeHandlerCountry}
          ref = {refCountry}
        >
          <option value="Не выбрано">Не выбрано</option>
          <option value="Индифферентные">Индифферентные</option>
          <option value="Коммунистические">Коммунистические</option>
          <option value="Социалистические">Социалистические</option>
          <option value="Умеренные">Умеренные</option>
          <option value="Либеральные">Либеральные</option>
          <option value="Консервативные">Консервативные</option>
          <option value="Монархические">Монархические</option>
          <option value="Ультраконсервативные">Ультраконсервативные</option>
          <option value="Либертарианские">Либертарианские</option>
        </select>
      </div>
      <div className="flex_input_edit">
        <label>Мировоззрение:</label>
        <select type="selector" value={religion} onChange={changeHandlerCity}ref = {refCity}>
          <option value="Не выбрано">Не указано</option>
          <option value="Атеизм">Атеизм</option>
          <option value="Православие">Православие</option>
          <option value="Католицизм">Католицизм</option>
          <option value="Протестанизм">Протестанизм</option>
          <option value="Ислам">Ислам</option>
          <option value="Буддизм">Буддизм</option>
          <option value="Конфуцианство">Конфуцианство</option>
          <option value="Светиский гумманизм">гумманизм</option>
          <option value="Пастафарианство">Пастафарианство</option>
        </select>
      </div>
      <div className="flex_input_edit">
        <label>Главное в жизни:</label>
        <select type="selector" value={favInLife} onChange={changeHandlerPhone}ref = {refPhone}>
          <option value="Не выбрано">Не выбрано</option>
          <option value="Семья и дети">Семья и дети</option>
          <option value="Карьера и деньги">Карьера и деньги</option>
          <option value="Развлечения и отдых">Развлечения и отдых</option>
          <option value="Наука и исследования">Наука и исследования</option>
          <option value="Совершенствование мира">Совершенствование мира</option>
          <option value="Саморазвитие">Саморазвитие</option>
          <option value="Красота и искусство">Красота и искусство</option>
          <option value="Слава и влияние">Слава и влияние</option>
        </select>
      </div>
      <div className="flex_input_edit">
        <label>Главное в людях:</label>
        <select
          type="selector"
          value={favInPeople}
          onChange={changeHandlerPhones}
          ref = {refDopPhone}
        >
          <option value="Не указано">Не указано</option>
          <option value="Ум и креативность">Ум и креативность</option>
          <option value="Доброта и честность">Доброта и честность</option>
          <option value="Красота и здоровье">Красота и здоровье</option>
          <option value="Власть и богатство">ласть и богатство</option>
          <option value="Смелость и упорство">Смелость и упорство</option>
          <option value="Юмор и жизнелюбие">Юмор и жизнелюбие</option>
        </select>
      </div>
      <div className="flex_input_edit">
        <label>Отношение к курению:</label>
        <select
          type="selector"
          value={attitudeToSmoking}
          onChange={changeHandlerPhones1}
          ref = {refDopPhone1}
        >
          <option value="Не выбрано">Не указано</option>
          <option value="Резко негативное">Резко негативное</option>
          <option value="Негативное">Негативное</option>
          <option value="Компромиссное">Компромиссное</option>
          <option value="Нейтральное">Нейтральное</option>
          <option value="Положительное">Положительное</option>
        </select>
      </div>
      <div className="flex_input_edit">
        <label>Отношение к алкоголю:</label>
        <select
          type="selector"
          value={attitudeToAlcohol}
          onChange={changeHandlerPhones2}
          ref = {refDopPhone2}
        >
          <option value="Не выбрано">Не указано</option>
          <option value="Резко негативное">Резко негативное</option>
          <option value="Негативное">Негативное</option>
          <option value="Компромиссное">Компромиссное</option>
          <option value="Нейтральное">Нейтральное</option>
          <option value="Положительное">Положительное</option>
        </select>
      </div>
      <div className="flex_input_edit">
        <label>Вдохновляют:</label>
        <input type="text" value={InspiredBy} onChange={changeHandlerPhones3} ref = {refDopPhone3}/>
      </div>
      <div id="polosa_edit" class="polosa_bottom_about_me"></div>
      <div onClick={changeView}>
        <button onClick={updProfile7} className="bt_save_edit">
          Сохранить
        </button>
      </div>
    </div>
  );
};

export default Personal_page;
